function randInt(min,max)
{
  return Math.floor(Math.random() * (max - min + 1) + min);
}

function randomPoints()
{
	var top = randInt(0, 799),
      bottom = randInt(top, 800);

	return '0,' + top + ' 800,' + top + ' 800,' + bottom + ' 0,' + bottom;
}

setInterval(function()
  {
    document.querySelectorAll('.glitch').forEach(function(glitch)
      {
        glitch.setAttribute('points', randomPoints());
      }
    );
  }, 50);



