$("body").keydown(function(e) {
  if(e.keyCode == 37) { // left
    $("#showroom").animate({
      left: "-=980"
    });
  }
  else if(e.keyCode == 39) { // right
    $("#showroom").animate({
      left: "+=980"
    });
  }
});

function align(dis){
  
  // get index of item
  var index = dis.index() + 1;
  
  // add active class to the new item
  $(".list-wrap li").removeClass("active");
  dis.delay(100).queue(function() {
    dis.addClass('active').dequeue();
  });
  
  var width = (window.innerWidth > 0) ? window.innerWidth : screen.width;
  console.log(width);
  // move the wave
  if(width > 1500){
    var left = index * 80 - 98; 
  }
  else if(width < 1500){
    var left = index * 67 - 98; 
  }
  
  $("#wave").css('left', left);
  
  // ‚ñº this is not necessary for the navigation ‚ñº
  
  // set the background color
  var color = dis.data('color');
  $(".slider").css('background', color); //body
  
  // set the text
  $(".page").text(dis.attr("title"));
}












